body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.avatarSuccess {
  background-color: rgb(76, 175, 80)
}
.rdw-link-decorator-wrapper {
  position: relative;
  background-color: #2ec866;
  padding: 2%;
  text-decoration: none !important;
  border-radius: 4px;
  color: #fff;
  margin-left: 30%;
}
.rdw-link-decorator-wrapper a span {
  color: #fff;    
  font-family: 'Avenir','Segoe UI','Ubuntu','Helvetica Neue','Helvetica',Helvetica,Arial,sans-serif;
  font-size: 16px;
  font-weight: bold
}
A {
  text-decoration: none;
}
.public-DraftStyleDefault-block .public-DraftStyleDefault-ltr{
  margin-left: 30%;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.large-svg {
  -webkit-transform: scale(12.8);
          transform: scale(12.8);
  color: #831057;
  margin-bottom: 0
}

.divContainer {
    padding: 4%
}

.noBorderRadius {
  border-radius: 0px !important;
}

@media only screen and (min-width: 375px) and (max-width: 969px) {
  .info_text {
    font-size: 34px;
  }
}
@media only screen and (min-width: 969px) and (max-width: 1280px) {
  .info_text {
    font-size: 34px;
  }
}

.wrapper-class {
  padding: 1rem;
  border: 1px solid #ccc;
}
.editor-class {
  background-color:lightgray;
  padding: 1rem;
  border: 1px solid #ccc;
}
.toolbar-class {
  border: 1px solid #ccc;
}
