.rdw-link-decorator-wrapper {
  position: relative;
  background-color: #2ec866;
  padding: 2%;
  text-decoration: none !important;
  border-radius: 4px;
  color: #fff;
  margin-left: 30%;
}
.rdw-link-decorator-wrapper a span {
  color: #fff;    
  font-family: 'Avenir','Segoe UI','Ubuntu','Helvetica Neue','Helvetica',Helvetica,Arial,sans-serif;
  font-size: 16px;
  font-weight: bold
}
A {
  text-decoration: none;
}
.public-DraftStyleDefault-block .public-DraftStyleDefault-ltr{
  margin-left: 30%;
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.large-svg {
  transform: scale(12.8);
  color: #831057;
  margin-bottom: 0
}

.divContainer {
    padding: 4%
}

.noBorderRadius {
  border-radius: 0px !important;
}

@media only screen and (min-width: 375px) and (max-width: 969px) {
  .info_text {
    font-size: 34px;
  }
}
@media only screen and (min-width: 969px) and (max-width: 1280px) {
  .info_text {
    font-size: 34px;
  }
}

.wrapper-class {
  padding: 1rem;
  border: 1px solid #ccc;
}
.editor-class {
  background-color:lightgray;
  padding: 1rem;
  border: 1px solid #ccc;
}
.toolbar-class {
  border: 1px solid #ccc;
}